h1 {
  font-family: acme;
  font-size: 3rem;
  text-align: center;
  margin: 0;
  border: 2px solid #0091da;
  border-radius: 5px;
  /*color: #0091da;*/
  width: 35vw;
  min-width: 300px;
  padding: 5px;
}
.bluetitle {
  color: #0091da;
  text-align: center;
}
.othertitle{
  color: #6C6E70;
  font-size: 2rem;
}
.title {
  text-decoration: none;
  /*color: #0091da;*/
}
.intro1{
  text-align: center;
  font-size: 0.8rem;
  margin: 5px 0;
  /*border-left: 3px solid #0091da;
  border-right: 3px solid #0091da;*/
  width: 30vw;
  min-width: 300px;
  padding: 5px;
}
.intro1:hover{
  transform: scale(1.5);
  transition: transform 1s;
}
.intro2 {
  font-size: 1rem;
  margin: 5px 0;
  border: 2px solid #0091da;
  border-radius: 5px;
  padding: 5px;
  text-align: center;
  width: 35vw;
  min-width: 300px;
  height: auto;
}
.number{
  font-size: 1.4rem;
}
.Result{
  border: 3px solid #0091da;
  text-align: center;
  border-radius: 5px;
  width: 35vw;
  min-width: 300px;
  padding: 5px;
}
.box {
  font-size: 1.5rem;
  text-align: center;
  margin: 0;
}
.notExist{
  text-align: center;
  font-size: 1.5rem;
}
.container {
  min-width: 330px;
  width: 40vw;
  margin: 0 auto;
  padding: 20px 5px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

footer {
  font-size: 0.5rem;
  margin-top: 10vh;
  border-top: 1px solid #bbb;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}



label {
  display: block;
  margin-bottom: 10px;
}
input:focus{outline: none;}
input[type="text"] {
  padding: 5px;
  border: 1.5px solid #bbb;
  border-radius: 5px;
  font-size: 1rem;
  width: 35vw;
  text-align: center;
  min-width: 300px;
}
input[type="text"]:active {
  transform: scale(1.05);
  transition: transform 0.2s;
}

button {
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
}

button[type="submit"] {
  background-color: #0091da;
  color: #fff;
  border: 3px solid #0091da;
  margin-bottom: 10px;
  width: 10vw;
  min-width: 120px;
}


button[type="button"] {
  background-color: #fff;
  color: #0091da;
  border: 3px solid #0091da;
  margin-bottom: 10px;
  width: 10vw;
  min-width: 120px;
}
button[type="submit"]:active {
  background-color: white;
  color: #0091da;
  border: 3px solid #0091da;
  transform: scale(1.1);
  transition: transform 0.2s;
}
button[type="button"]:active {
  background-color: #0091da;
  color: #fff;
  transform: scale(1.1);
  transition: transform 0.2s;
}
ul {
  list-style: none;
  padding-left: 0;
  padding: 0 10px 0 10px;
}

li {
  font-size: 1rem;
}
footer{
  text-align: center;
  font-size: 0.5rem;
}
.email{
  text-decoration: none;
  color: black;
}
.freecountercode {
  text-align: center;
  height: fit-content;
}
::selection{
  background-color: yellow;
}

